.button {
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  text-decoration: none;
  text-align: center;
  border: 1px solid #e5ddd5;
  padding: 13px 0;
  margin-right: 20px;
  margin-left: 20px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: border 0.3s ease;
}

.button--footer {
  color: #ffffff;
}

.button--footer:hover,
.button--footer:focus {
  outline: none;
  border-color: #777777;
}

.button--footer:active {
  opacity: 0.6;
  border: none;
}

.button--socials {
  width: auto;
}

.button--to-all-news:hover,
.button--to-all-news:focus {
  outline: none;
  border-color: #777777;
}

.button:active {
  opacity: 0.6;
  border: none;
}

.button--rewiews:hover,
.button--rewiews:focus {
  outline: none;
  border-color: #777777;
}

.button--rewiews:active {
  opacity: 0.6;
  border: none;
}

.button--phone {
  color: #ffffff;
  border-color: #4e4e4e;
}

.button--phone:hover,
.button--phone:focus {
  outline: 1px solid;
}

.button--form-submit {
  width: 100%;
  background-color: #826550;
  color: #ffffff;
}

.button--form-submit:hover,
.button--form-submit:focus {
  background-color: #6a4d38;
}

.button--form-submit:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--form-submit:disabled {
  background-color: #aeaeae;
  cursor: not-allowed;
}

.button--copyright {
  color: #ffffff;
  border-color: #4e4e4e;
}

.button--form-submit {
  width: 100%;
  margin: 0;
}

* {
