/*main-nav*/

.main-nav {
  background-color: #826550;
}

.main-nav__wrapper {
  opacity: 0;
  transform: translateX(-550px);
  transition: transform 0.7s ease-out, min-height 0.2s ease-out;
  min-height: 0;
  height: 0;
}

.main-nav__wrapper.shown {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  transform: translateX(0);
  opacity: 1;
  min-height: 250px;
  height: auto;
}

.main-nav__toggle-button-wrapper {
  display: flex;
  width: auto;
}

.main-nav__text-mobile {
  flex-grow: 1;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  text-align: right;
  padding-right: 46px;
  font-weight: 700;
  padding-block: 13px;
  margin: 0;
}

.main-nav__text-mobile--hidden {
  display: none;
}

.main-nav__toggle {
  width: 70px;
  background-color: #6a4d38;
  border: none;
  position: relative;
  height: 50px;
  padding: 0;
}

.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  width: 36px;
  height: 2.5px;
  background-color: #ffffff;
  box-shadow: 0 8px #ffffff, 0 16px #ffffff;
}

.main-nav__toggle--opened {
  position: absolute;
  z-index: 1;
}

.main-nav__toggle--opened::after,
.main-nav__toggle--opened::before {
  content: "";
  position: absolute;
  top: 22px;
  left: 17px;
  width: 36px;
  height: 2.5px;
  background-color: #ffffff;
  box-shadow: none;
}

.main-nav__toggle--opened::before {
  transform: rotate(45deg);
}

.main-nav__toggle--opened::after {
  transform: rotate(-45deg);
}

.site-list {
  list-style: none;
  display: flex;
  flex-grow: 1;
  margin: 0;
  padding: 0;
  flex-direction: column;
  width: 78.125%;
  transition: 0.5s;
}

.site-list-nav__link {
  display: block;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  font-family: inherit;
  position: relative;
  padding-inline: 20px;
  padding-block: 13px;
  box-shadow: 0px 1px 0px #71543f, inset 0px 1px 0px #886e5b;
  position: relative;
  transition: 0.5s;
}

.site-list-nav__link:hover,
.site-list-nav__link:focus {
  color: #000000;
}

.site-list-nav__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  transition: 0.5s;
  transform-origin: right;
  transform: scaleX(0);
  z-index: -1;
}

.site-list-nav__link:hover::before,
.site-list-nav__link:focus::before {
  transition: transform 0.5s;
  transform-origin: left;
  transform: scaleX(1);
}

.site-list-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}

.site-list-nav__link--active:hover,
.site-list-nav__link--active :focus {
  background-color: #000000;
  color: #ffffff;
  cursor: auto;
}

.site-list-nav__link--active:hover::before,
.site-list-nav__link--active :focus::before {
  content: unset;
}

.site-list-nav__link--active::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 17px;
  right: 17px;
  height: 3px;
  background-color: #826550;
}

.site-list-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.site-list:hover .site-list-nav__item .site-list-nav__link {
  transform: scale(1);
}

